.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 100;
  background: rgba(0, 0, 0, .2);
  transition: opacity .3s, visibility 3s;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden .modal {
  transform: scale(.5)skew(20deg, 70deg)translate(100px, 100px);
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal {
  width: 288px;
  height: 413px;
  transform: scale(1) skew(0) translate(-50%, -50%);
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 20px;
  transition: transform .3s;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: hidden;
}

